@import "../styles/settings/normalize";
@import "../styles/settings/settings";

@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700;900&display=swap");

html {
  --BodyBackgroundColor: radial-gradient(ellipse at center, #efefef 0%, #a2b3bf 100%);
  --BodyTextColor: #171717;
  --BodyLineBackgroundColor: rgba(0, 0, 0, 0.08);
  --HomeInfoContainerBackgroundColor: radial-gradient(ellipse at center, #efefef 0%, #a2b3bf 100%);
  --ButtonBackgroundColor: rgba(0, 0, 0, 0.7);
  --AboutStackBackgroundColor: rgba(255, 255, 255, 0.7);
  --HomeAvatarBorderColor: #171717;
  --langBtnColor: rgba(0,0,0,.5);
  --modalBackgroundColor: rgba(255, 255, 255, 0.4);
  --modalShadowGradientBottom: linear-gradient(to bottom, #ffffff00 0%, #ffffff 110%);
  --modalShadowGradientTop: linear-gradient(to top, #ffffff00 0%, #ffffff 110%);
}

@media (prefers-color-scheme: dark) {
  html.dark {
    --BodyBackgroundColor: linear-gradient(-45deg, #171717, rgb(33, 20, 38), rgb(48, 22, 52), rgb(38, 9, 37));
    --BodyTextColor: #fff;
    --BodyLineBackgroundColor: rgba(255, 255, 255, 0.1);
    --HomeInfoContainerBackgroundColor: rgba(0, 0, 0, 0.2);
    --ButtonBackgroundColor: rgba(0, 0, 0, 0.9);
    --AboutStackBackgroundColor: rgba(0, 0, 0, 0.2);
    --HomeAvatarBorderColor: greenyellow;
    --langBtnColor:rgba(255,255,255,.4);
    --modalBackgroundColor: rgba(0, 0, 0, 0.2);
    --modalShadowGradientBottom: linear-gradient(to bottom, #00000000 0%, #00000080 100%);
    --modalShadowGradientTop: linear-gradient(to top, #00000000 0%, #000000 110%);
  }
}

html,
body {
  font-family: $font-family;
  font-weight: $font-weight-regular;
  color: var(--BodyTextColor);
}

html.dark body {
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

body {
  // linear-gradient(-45deg, #171717, rgb(33, 20, 38), rgb(48, 22, 52), rgb(38, 9, 37));
  background: var(--BodyBackgroundColor);
  // background-color: #171717;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

a {
  color: $color-links;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 3.5rem;
  color: var(--BodyTextColor);
  span {
    color: rgb(115, 112, 119);
    font-size: 2.5rem;
  }
}
h3 {
  font-size: 2rem;
  margin-top: 0;
  color: var(--BodyTextColor);
}
.downloadMe {
  position: absolute;
  top: 1rem;
  right: 10%;
  animation: downloadme-rotate 2s infinite;
  pointer-events: none;
  z-index: -1;
  @media (min-width: 576px) {
    right: 32%;
  }
  @media (min-width: 768px) {
    right: 19rem;
  }
}
@keyframes downloadme-rotate {
  0% {
    transform: rotate(0) scale(1);
  }

  60% {
    transform: rotate(0) scale(1);
  }

  75% {
    transform: rotate(0) scale(1.12);
  }

  80% {
    transform: rotate(0) scale(1.1);
  }

  84% {
    transform: rotate(-10deg) scale(1.1);
  }

  88% {
    transform: rotate(10deg) scale(1.1);
  }

  92% {
    transform: rotate(-10deg) scale(1.1);
  }

  96% {
    transform: rotate(10deg) scale(1.1);
  }

  to {
    transform: rotate(0) scale(1);
  }
}


.sr-only {
  display: none;
}
button {
  border:0;
  background: transparent;
  outline: none;
}