@import "../styles/settings/settings";
.backIcon {
  transform: scaleX(-1);
  margin-right: 1rem;
}
.contactLinks {
  font-size: 2rem;
  display: flex;
  transition: all 0.2s ease-in-out;

  &:hover {
    a {
      opacity: 0.5;
      filter: saturate(0.5);
    }
  }
  a {
    padding: 1rem;
    transition: all 0.2s ease-in-out;
    &:hover {
      transform: scale(1.5);
      opacity: 1;
      filter: saturate(1);
    }
  }
}
.infoContainer {
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0px 8px 48px rgb(81 97 109 / 16%);
  margin-bottom: 1rem;
  background-color: var(--HomeInfoContainerBackgroundColor);
  backdrop-filter: blur(7px);
}

