.lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  margin: auto;
  width: 90vw;
  z-index: -1;
}

.line {
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0;
  left: 50%;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;

  &:after {
    content: "";
    display: block;
    position: absolute;
    height: 15vh;
    width: 100%;
    top: -50%;
    left: 0;
    animation: drop 4s 0s infinite;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }

  &:nth-child(1) {
    margin-left: 50%;
    &:after {
      animation-delay: 0.5s;
    }
  }

  &:nth-child(2) {
    margin-left: 40%;
    &:after {
      animation-delay: 1s;
    }
  }

  &:nth-child(3) {
    margin-left: 30%;
    &:after {
      animation-delay: 1.3s;
    }
  }

  &:nth-child(4) {
    margin-left: 20%;
    &:after {
      animation-delay: 1.4s;
    }
  }

  &:nth-child(5) {
    margin-left: 10%;
    &:after {
      animation-delay: 0.3s;
    }
  }

  &:nth-child(6) {
    margin-left: 0%;
    &:after {
      animation-delay: 1s;
    }
  }

  &:nth-child(7) {
    margin-left: -10%;
    &:after {
      animation-delay: 1.2s;
    }
  }
  &:nth-child(8) {
    margin-left: -20%;
    &:after {
      animation-delay: 0.9s;
    }
  }
  &:nth-child(9) {
    margin-left: -30%;
    &:after {
      animation-delay: 0.7s;
    }
  }
  &:nth-child(10) {
    margin-left: -40%;
    &:after {
      animation-delay: 1.1s;
    }
  }
  &:nth-child(11) {
    margin-left: -50%;
    &:after {
      animation-delay: 0.2s;
    }
  }
}

@keyframes drop {
  0% {
    top: -50%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, blueviolet 75%, #ffffff 100%);
  }
  50% {
    top: 110%;
  }
  100% {
    top: -110%;
    background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, blueviolet 75%, #ffffff 100%);
  }
}

p.large {
  font-size: 1.4rem;
  line-height: 1.5;
}

.customDetails {
  border: 2px solid rgba(0, 0, 0, 0.2);
  color: gray;
  margin-bottom: 0.5rem;
  border-radius: 0.6rem;
  background-color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  &:hover {
    border: 2px solid rgba(blue, 0.4);
  }
}

details summary:focus {
  outline: none;
}
details summary::-webkit-details-marker {
  display: none;
}
details {
  margin-bottom: 0 !important;
}
details + *:not(details) {
  margin-top: 1em;
}
details summary {
  cursor: pointer;
  list-style: none;
  position: relative;
  padding: 0.25rem 1.25rem 0.25rem 2.5rem;
  font-weight: 700;
  line-height: 1.3;
  transition: 0.3s;
}
details summary::before,
details summary::after {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 100%;
  position: absolute;
  top: 0;
  font-family: "FontAwesome";
  font-weight: normal;
  line-height: 1;
}
details summary::before {
  left: 0;
  font-size: 2rem;
  font-weight: bold;
  margin-top: -2px;
  content: "\203A";
}
details[open] summary::before {
  transform: rotate(90deg);
}

details > div {
  padding: 0.5rem 1.25rem 1.5rem 1.25rem;
}
details > div > * {
  margin-top: 0;
  margin-bottom: 1rem;
}
details > div > *:last-child,
details > div > div > *:last-child {
  margin-bottom: 0;
}

details[open] summary ~ * {
  animation: sweep 0.5s ease-in-out;
}
@keyframes sweep {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.customDetails summary:hover,
.customDetails[open] summary {
  color: rgba(0, 0, 0, 0.8);
  background-color: rgba(255, 255, 255, 1);
}
.customDetails summary {
  transition: all 0.1s ease-in-out;
  border-radius: 1rem;
  padding: 0.75rem 1.25rem 0.75rem 2.5rem;
}
.customDetails > div {
  padding: 1.5rem;
  border-top: 1px solid #000;
  border-bottom: none;
}

.customDetails > div {
  border-top: none;
}
