@import "../styles/settings/settings";

.projectList {
  padding: 0;
  li {
    list-style-type: none;
  }
  a {
    &:hover {
      position: relative;
      &:after {
        content: "";
        position: absolute;
        bottom: -5px;
        left: 0;
        height: 2px;
        width: 100%;
        background-color: $color-links;
      }
    }
  }
}

.backIcon {
  transform: scaleX(-1);
  margin-right: 1rem;
}
.infoContainer {
  border-radius: 16px;
  padding: 0.5rem 2rem;
  box-shadow: 0px 8px 48px rgb(81 97 109 / 16%);
  margin-bottom: 1rem;
  background-color: var(--HomeInfoContainerBackgroundColor);
  backdrop-filter: blur(7px);
}

