@import "../styles/settings/settings";

.title {
  font-size: 3em;
}

.username {
  font-size: 1.5rem;
  color: gray;
}

.backIcon {
  transform: scaleX(-1);
  margin-right: 1rem;
}

.header {
  font-size: 2em;
  margin-top: 2em;

  &:first-child {
    margin-top: 0;
  }
}

.profile {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto;

  img {
    clip-path: url(#ambossLogo);
  }
}

.glitch {
  animation: glitch 0.3s ease infinite reverse;
}
@keyframes glitch {
  0% {
    filter: drop-shadow(-4px 4px 0px purple) drop-shadow(-4px 4px 0px cyan) drop-shadow(4px -4px 0px magenta);
  }
  25% {
    filter: drop-shadow(-4px -4px 0px purple) drop-shadow(-4px -4px 0px cyan) drop-shadow(-4px 4px 0px magenta);
  }
  50% {
    filter: drop-shadow(2px 2px 0px purple) drop-shadow(-2px 2px 0px cyan);
  }
  75% {
    filter: drop-shadow(2px -4px 0px purple) drop-shadow(4px -2px 0px cyan);
  }
  100% {
    filter: drop-shadow(-2px 2px 0px purple) drop-shadow(2px -2px 0px cyan) drop-shadow(4px -4px 0px magenta);
  }
}


.profileContent {

  p,
  ul {
    &:first-child {
      margin-top: 0;
    }
  }
}

.profileBio {
  font-size: 1.5rem;
}

.profileCompany,
.profileStats,
.profileTwitter {
  font-size: 1.2em;
}

.profileStats {
  list-style: none;
  padding: 0;

  strong {
    font-weight: 600;
  }
}

