  .stack-logos-logos {
    transform: scale(1.1);
  }
}

@media only screen and (min-width: 1440px) {
  .stack-logos-logos {
    transform: scale(1.3);
    filter: blur(0);
    opacity: 1;
    animation: homeScrollLogosAppear 4s cubic-bezier(0.165, 0.84, 0.44, 1) 1;
  }
}

@keyframes homeScrollLogosAppear {
  0% {
    opacity: 0;
    filter: blur(10px);
    transform: scale(4);
  }
  100% {
    opacity: 1;
    filter: blur(0);
    transform: scale(1.3);
  }
}

@keyframes homeScrollLogos {
  from {
    transform: translateX(-16px);
  }

  to {
    transform: translateX(-1572px);
  }
}

@keyframes homeRotateLogos {
  from {
    transform: rotate(-16deg);
  }

  to {
    transform: rotate(-376deg);
  }
}

@keyframes homeKeepLogosUpright {
  from {
    transform: rotate(16deg);
  }

  to {
    transform: rotate(376deg);
  }
}

@keyframes homeLogoDrift1 {
  0% {
    transform: translate(0);
  }

  25% {
    transform: translate(4px, 3px);
  }

  50% {
    transform: translate(-4px, -1px);
  }

  75% {
    transform: translate(3px, -3px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes homeLogoDrift2 {
  0% {
    transform: translate(0);
  }

  25% {
    transform: translate(-3px, -2px);
  }

  50% {
    transform: translate(4px, 2px);
  }

  75% {
    transform: translate(-3px, -1px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes homeLogoDrift3 {
  0% {
    transform: translate(0);
  }

  25% {
    transform: translate(-5px, -3px);
  }

  50% {
    transform: translate(-2px, -4px);
  }

  75% {
    transform: translate(5px, 2px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes homeLogoDrift4 {
  0% {
    transform: translate(0);
  }

  25% {
    transform: translate(0, 4px);
  }

  50% {
    transform: translate(-1px, -2px);
  }

  75% {
    transform: translate(1px, 2px);
  }

  100% {
    transform: translate(0);
  }
}
