@import "../styles/settings/settings";

.modeSwitch {
  background: transparent;
  border: 0;
  cursor: pointer;
}
.turnMeOn,
.turnMeOff {
  position: relative;
    &:after {
      content: attr(data-message);
      position: absolute;
      top: -10%;
      left: -145%;
      width: 90px;
      opacity: 0;
      background-color: white;
      color: black;
      box-shadow: 2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02), 6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
        12.5px 12.5px 10px rgba(0, 0, 0, 0.035), 22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
        41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05), 100px 100px 80px rgba(0, 0, 0, 0.07);
      border-radius: 20px 20px 4px 20px;
      padding: .5rem;
      transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
      pointer-events: none;
      transform: scale(.8);
      transform-origin: bottom right;
      font-size: 14px;
    }
    &:hover {
      cursor: pointer;
      &::after {
        opacity: 1;
        transform: scale(1);
        top: -20%;
      left: -155%;
      }
    }
}
.fireOn {
  filter: drop-shadow(0px 0px 2px white) drop-shadow(0px 0px 10px cyan);
}
.fireOff {
  filter: drop-shadow(0px 0px 2px white) drop-shadow(0px 0px 10px red);
}

.contactLinks {
  font-size: 2rem;
  display: flex;
  transition: all 0.2s ease-in-out;

  &:hover {
    a {
      opacity: 0.5;
      filter: saturate(0.5);
    }
  }
  a {
    padding: 1rem;
    transition: all 0.2s ease-in-out;
    &:hover {
      transform: scale(1.5);
      opacity: 1;
      filter: saturate(1);
    }
  }
}

.infoContainer {
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0px 8px 48px rgb(81 97 109 / 16%);
  margin-bottom: 1rem;
  background-color: var(--HomeInfoContainerBackgroundColor);
  backdrop-filter: blur(7px);
  display: flex;
  flex-direction: column;
  text-align: center;
@media (min-width: 768px) {
  gap: 2rem;
  }
  @media (min-width: 992px) {
    flex-direction: row;
    text-align: left;
    padding: 0.5rem 2rem;
  }
  &.alignCenter {
    align-items: center;
  }
  &.alignEnd {
    align-items: flex-end;
  }

  .avatar {
    height: 160px;
    width: 160px;
    position: relative;
    &:after {
      content: attr(data-message);
      position: absolute;
      top: -6%;
      left: -95%;
      width: 157px;
      opacity: 0;
      background-color: white;
      color: black;
      box-shadow: 2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02), 6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
        12.5px 12.5px 10px rgba(0, 0, 0, 0.035), 22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
        41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05), 100px 100px 80px rgba(0, 0, 0, 0.07);
      border-radius: 20px 20px 4px 20px;
      padding: 1rem;
      transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
      pointer-events: none;
      transform: scale(.8);
      transform-origin: bottom right;
    }
    &:hover {
      cursor: help;
      &::after {
        opacity: 1;
        transform: scale(1);
        top: -12%;
      left: -100%;
      }
    }

    &:hover {
      .border {
        border: 10px groove var(--HomeAvatarBorderColor);
      }
    }

    .border {
      border: 4px dashed var(--HomeAvatarBorderColor);
      border-radius: 50%;
      bottom: 0;
      height: 160px;
      width: 160px;
      left: 0;
      margin: auto;
      position: absolute;
      right: 0;
      top: 0;
      animation: spin 15s linear 1s infinite;
    }

    .photo {
      border-radius: 50%;
      height: 160px;
      width: 160px;
      max-width: 160px;
      z-index: 1;
    }
  }
}

.modeAndPageCounter {
  display: flex;
  align-items: center;
  color: gray;
}
.langNav {
  display: flex;
  gap: 1rem;
}


.langBtn {
  border:0;
  background: transparent;
  color: var(--langBtnColor);
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color .15s ease-in-out, color .15s ease-in-out;

  &:hover {
    background-color: rgba(0,0,0,.4);
    color: white;
  }

  &.langActive {
    font-weight: bold;
    background-color: rgba(0,0,0,.6);
    color: white;
  }
}

.modeAndCountContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1rem;
  @media (min-width: 768px) {
    flex-direction: row;
    gap: 0;
    margin-bottom: 0;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

