.lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  margin: auto;
  width: 90vw;
  z-index: -1;
}

.line {
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0;
  left: 50%;
  background: var(--BodyLineBackgroundColor);
  overflow: hidden;

  &:after {
    content: "";
    display: block;
    position: absolute;
    height: 15vh;
    width: 100%;
    top: -50%;
    left: 0;
    animation: drop 2.5s infinite forwards ease-in-out;
  }

  &:nth-child(1) {
    margin-left: 50%;
    &:after {
      animation-delay: 0.5s;
    }
  }

  &:nth-child(2) {
    margin-left: 40%;
    &:after {
      animation-delay: 1s;
    }
  }

  &:nth-child(3) {
    margin-left: 30%;
    &:after {
      animation-delay: 1.3s;
    }
  }

  &:nth-child(4) {
    margin-left: 20%;
    &:after {
      animation-delay: 1.4s;
    }
  }

  &:nth-child(5) {
    margin-left: 10%;
    &:after {
      animation-delay: 0.3s;
    }
  }

  &:nth-child(6) {
    margin-left: 0%;
    &:after {
      animation-delay: 1s;
    }
  }

  &:nth-child(7) {
    margin-left: -10%;
    &:after {
      animation-delay: 1.2s;
    }
  }
  &:nth-child(8) {
    margin-left: -20%;
    &:after {
      animation-delay: 0.9s;
    }
  }
  &:nth-child(9) {
    margin-left: -30%;
    &:after {
      animation-delay: 0.7s;
    }
  }
  &:nth-child(10) {
    margin-left: -40%;
    &:after {
      animation-delay: 1.1s;
    }
  }
  &:nth-child(11) {
    margin-left: -50%;
    &:after {
      animation-delay: 0.2s;
    }
  }
}

@keyframes drop {
  0% {
    top: -50%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, blueviolet 75%, #ffffff 100%);
  }
  50% {
    top: 110%;
  }
  100% {
    top: -110%;
    background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, blueviolet 75%, #ffffff 100%);
  }
}

p.large {
  font-size: 1.4rem;
  line-height: 1.5;
}


