/* MODAL */

.modalBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1040;
  background-color: rgba(#3c3ca6, 50%);
  animation: opacity alternate 0.15s ease-in-out;
  backdrop-filter: blur(10px);
  overflow: auto;
}
@keyframes opacity {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

.modal {
  overflow: auto;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1050;
  outline: 0;
  display: flex;
  align-items: center;
}

.searchButton {
  background:transparent;
  border: none;
  color: var(--langBtnColor);
  padding: .5rem 1rem;
  border-radius: .5rem;
  gap: .5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: background-color .15s ease-in-out, color .15s ease-in-out;
  &:hover {
    background-color: rgba(0,0,0,.4);
    color: white
  }
}

.colleagueLink {
  display: block;
  background:transparent;
  border: none;
  text-align: left;
  cursor: pointer;
  width: 100%;
  padding: 1rem;
  color: var(--langBtnColor);
  border-radius: .5rem;
  text-align: center;
  transition: background-color .15s ease-in-out, color .15s ease-in-out, transform .15s ease-in-out;
  &:hover {
    background-color: rgba(0,0,0,.4);
    color: white;
    transform: scale(1.2);
    opacity: 1;
  }
  span {
    display: block;
  }
  .userName {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: .5rem;
  }
  .handle {
    font-size: .8rem;
  }
}

.modalDialog {
  margin: 0.5rem;
  position: relative;
  background-color: var(--modalBackgroundColor);
  padding: 6rem 4rem;
  border-radius: 16px;
  overflow: auto;
  width: 100%;
  height: 50vh;
  animation: modalMove alternate 1 0.15s ease-in-out;
  
  @media (min-width: 576px) {
    width: 500px;
    margin: 1.75rem auto;
  }
  &::-webkit-scrollbar {
    display: none;
  }

  &::before,
  &:after {
    content: "";
    position: fixed;
    height: 180px;
    left: 50%;
    transform: translateX(-50%);
    width: 500px;
    pointer-events: none;
  }
  &:after {
    bottom: calc(50vh - 25vh);
    background: var(--modalShadowGradientBottom);
    border-radius: 0 0 16px 16px;
  }
  &:before {
    top: calc(50vh - 25vh);
    background: var(--modalShadowGradientTop);
    border-radius: 16px 16px 0 0;
  }
}

@keyframes modalMove {
  0% {
    transform: translateY(-50px);
  }
  100% {
    transform: translateY(0);
  }
}
