@import "../../styles/settings/settings";

.button {
  font-size: 1.5rem;
  color: white;
  > span {
    display: flex;
    flex-direction: row;
    @media (min-width: 768px) {
      flex-direction: column;
    }
    @media (min-width: 992px) {
      flex-direction: row;
    }
  }
  background-color: var(--ButtonBackgroundColor);
  border: 0;
  font-weight: $font-weight-black;
  font-family: inherit;
  text-transform: uppercase;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  cursor: pointer;
  position: relative;
  transition: background-color 0.2s ease-in-out;

  &:hover {
    background-color: transparent;
    box-shadow: 0 0 0 0.25rem rgba(0, 0, 0, 0.6);
  }
}

.buttonEffect {
  position: absolute;
  top: 1px;
  right: 1px;
  bottom: 1px;
  left: 1px;
  background: linear-gradient(-90deg, #007cf0, #00dfd8, #ff0080, #007cf0);
  background-size: 400% 100%;
  animation: buttonGradientEffect 8s ease-in-out infinite;
  z-index: -1;
  border-radius: 1rem;
  &:after {
    content: "";
    position: absolute;
    background-size: inherit;
    background-image: inherit;
    border-radius: inherit;
    animation: inherit;
    left: 0;
    right: 0;
    top: 2px;
    height: 100%;
    filter: blur(10px);
  }
}

@keyframes buttonGradientEffect {
  50% {
    background-position: 140% 50%;
    transform: skew(-2deg);
  }
}
